const cheatsheetConstants = {
  list: list.sort((a, b) => {
    const aVal = a.topic.toLowerCase();
    const bVal = b.topic.toLowerCase();
    if (aVal < bVal) //sort string ascending
     return -1;
    if (aVal > bVal)
     return 1;
    return 0; //default return value (no sorting)
  })
};

export default cheatsheetConstants;
